.likes-photos {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  width: 80%;
  margin: auto;
}

.likes-photo {
  margin-top: 20px;
  position: relative;
  width: 30%;
  height: 300px;
  margin-left: 3%;
  margin-bottom: 20px;
  transition: box-shadow 500ms;
  transition: opacity 800ms;
}

.likes-photo:hover {
  box-shadow: 8px 8px 10px gray;
}

.likes-photo img {
  width: 100%;
  height: 100%;
}

.remove-photo {
  position: absolute;
  right: 5px;
  font-size: 2vw;
  color: gray;
  cursor: pointer;
  opacity: 0;
  transition: opacity 500ms;
}
.no-likes {
  text-align: center;
  padding-top: 14%;
}

.no-likes .title {
  font-family: 'Dancing Script', cursive;
  font-size: 45px;
  -webkit-text-stroke: 1px #65D1D1;
  color: #E3F6F6;
}

.no-likes .smile {
  font-size: 35px;
}
